.app{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(231, 209, 220);
  
}

.container{
  display: flex;
  height: 80vh;
  border: 1px solid brown;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  color: brown;
}

.todoForm{
  display: flex;
  justify-content: space-around;
  width: 450px;
}

input{
  width: 80%;
  padding: 10px;
  border-radius: 20px;
  border: none;
}

button{
  border: none;
  padding: 10px;
  border-radius: 20px;
  margin: 0 5px;
}

.allTodos{
  list-style: none;
  margin: 0;
  padding: 0;

}

.singleTodo{
  border-radius: 50px;
  margin: 10px 0;
  background-color: rgb(199, 164, 185);
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
}

.todoText{
  flex: 1;
}